export default {
  2: "<p>In U.S. national party primaries, public financing is available to candidates who meet certain guidelines. During a specified time period, the candidates collect donations from their supporters. After the time period ends, each candidate is eligible to receive a portion of public funds based on each candidate's total receipts.</p><p>The problem with this system is that it inherently gives wealthy donors a distinct advantage by basing the payout from the public fund on the total sum of donations for each candidate. <p>In the U.S.A, this problem is imperfectly confronted by a $2,700 limit per candidate, per donor. While well-intentioned, this limit is arbitrary and pushes wealthy donors toward methods of exerting political influence that are subject to far less oversight (ex: Super PACs).</p>",
  3: "<p>In the 2016 primary, Hillary Clinton received the largest <em>total</em> donations while Bernie Sanders received the most <em>individual</em> donations.</p><p>Each green dot in the chart represents approximately 3,700 donations and the legend on the left lists their respective donation ranges. The yellow dots represent the portion of public funds made available to each candidate. The public funds help each candidate proportional to their total receipts and don't change the competitive balance between candidates, giving wealthy donors a significant advantage.</p>",
  4: ""
};
